const Item = ({item, onRemove}, props) => {
  

    return(
        <div className='box' onClick={() => onRemove(item.id)}>
            <li><p>{item.newitem}</p></li>
            <li><p className='quantity'>{item.quantity}</p></li>
            {/* <button onClick={() => onRemove(item.id)}>X</button> */}
            
            
            
        </div>
    )
}

export default Item;